<template>
	<view>
		<kt-nav-bar :highlight="true" :title="' '" backgroundColor="'rgba(0,0,0,0)"></kt-nav-bar>
		<view class="top-img" v-show="isFixed"></view>
		<view v-show="isFixed" style="
		position: fixed;
		width: 100%;
		top:0;
		left: 0;
		z-index:20">
			<view class="top-box">
				<view :style="{
					'height': statusBarHeight - (-navHeight) + 'px'
				}"></view>

				<view style="padding: 0 30rpx 0 30rpx;box-sizing: border-box;">



				</view>



			</view>
			<view style="height: 160rpx;"></view>

			<view style="background-color: #fff;
			border-radius: 100rpx 0 0 0;
			">
				<view style="background-color: #fff;
				padding: 20rpx 20rpx 20rpx 40rpx;
				border-radius: 100rpx 0 0 0;
				width: 100%;
				box-sizing: border-box;
				">

					<user-card :distanceInShow="false" :ovoUserInfo="ovoUserInfo"></user-card>
				</view>

				<!-- 分段器: 动态、资料 -->
				<view class="segmentation-box">
					<view @click="segmentationSelected = 'post'"
						:class="segmentationSelected == 'post' ? 'segmentation-item segmentation-box-item-active' : 'segmentation-item'">
						<text>动态</text>
					</view>
					<view @click="segmentationSelected = 'info'"
						:class="segmentationSelected == 'info' ? 'segmentation-item segmentation-box-item-active' : 'segmentation-item'">
						<text>资料</text>
					</view>
				</view>
			</view>
		</view>

		<scroll-view style="
		position: fixed;
		height: calc(100vh - 10rpx);
		width: 100%;
		top: 0;
		left: 0;
		" ref="scroll" @scroll="scroll" @scrolltolower="scrolltolower" :scroll-y="isScrollY">

			<view class="top-box">
				<view :style="{
					'height': statusBarHeight - (-navHeight) + 'px'
				}"></view>

				<view style="padding: 0 30rpx 0 30rpx;box-sizing: border-box;">
					<image :src="$kt.file.byPath('image/cover.png')" mode="widthFix" class="top-box-back-img"></image>


					<!-- 注册时间 -->
					<view v-if="false" class="create-time">
						<text>注册时间：</text>
						<text>
							{{
								$kt.util.dateFromat(
									$kt.util.gmtToLocalTime(ovoUserSelf.user.gmtCreate), "yyyy-MM-dd"
								)
							}}
						</text>

					</view>

				</view>



			</view>
			<view style="height: 500rpx;"></view>
			<view style="border-radius: 100rpx 0 0 0;background-color: #fff;">
				<view :style="{
							'opacity': isFixed ? '0' : '1'
						}" style="background-color: #fff;
				padding: 20rpx 20rpx 20rpx 40rpx;
				border-radius: 100rpx 0 0 0;
				width: 100%;
				box-sizing: border-box;
				">

					<user-card :distanceInShow="false" :ovoUserInfo="ovoUserInfo"></user-card>
				</view>


				<!-- 分段器: 动态、资料 -->
				<view :style="{
					'opacity': isFixed ? '0' : '1'
				}" class="segmentation-box">
					<view @click="segmentationSelected = 'post'"
						:class="segmentationSelected == 'post' ? 'segmentation-item segmentation-box-item-active' : 'segmentation-item'">
						<text>动态</text>
					</view>
					<view @click="segmentationSelected = 'info'"
						:class="segmentationSelected == 'info' ? 'segmentation-item segmentation-box-item-active' : 'segmentation-item'">
						<text>资料</text>
					</view>
				</view>

				<postList
				v-if="segmentationSelected == 'post'"
				 :isMe="isMe"></postList>


				<view style="background-color: #fff;position: fixed;bottom: 0;left: 0;
		width: 100%;height: calc(100vh - 10rpx - 600rpx);z-index: -1;"></view>
			</view>
		</scroll-view>

	</view>
</template>

<script>
import postList from './inUserInfo/postList.vue';

export default {
	components: {
		postList
	},
	data() {
		return {
			isScrollY: true,
			isPushing: false,
			isEnd: false,
			segmentationSelected: 'post',
			sexualOrientationMap: {},
			ovoUserInfo: {},
			ovoUserSelf: {},
			statusBarHeight: uni.getSystemInfoSync().statusBarHeight,
			navHeight: 48,
			isHasBack: false,
			screenTop: 0,
			isFixed: false,
			backgroundImage: '',
			// 是否是自己
			isMe: false,
		};
	},
	onLoad(options) {
		this.ovoUserSelf = this.$ovo.getSelf();
		this.ovoUserInfo = options.isMe == 1 ? this.ovoUserSelf : null;
		this.isMe = options.isMe == 1;
		if (this.isMe == 1) {
			this.isMe = true;
			this.ovoUserInfo = this.ovoUserSelf;
			this.getPostOfSelf();
		}

		this.sexualOrientationMap = this.$ovo.getSexualOrientationMap();
		this.backgroundImage = "url(\"" + this.$kt.file.byPath('image/cover.png') + "\");"
		this.navHeight = 44;
		// #ifdef MP-WEIXIN
		this.navHeight = uni.getSystemInfoSync().system.indexOf("ios") != -1 ? 44 : 48;
		// #endif

		// 判断是否还有上一页
		let pages = getCurrentPages();
		
		if (pages.length > 1) {
			this.isHasBack = true;
		}

	},
	methods: {
		scroll(res) {
			this.screenTop = res.target.scrollTop;
			if (this.screenTop > this.$kt.util.rpxToPx(460) - (this.navHeight + this.statusBarHeight)) {
				this.isFixed = true;
				return false;
			}
			this.isFixed = false;
		},
		scrolltolower(){
			uni.$emit("userInfoScrolltolower", this.segmentationSelected)
		}
	}
}
</script>

<style lang="scss" scoped>
.emotional-orientation-item {
	display: inline-block;
	padding: 10rpx 20rpx 10rpx 20rpx;
	height: 30rpx;
	line-height: 30rpx;
	margin: 10rpx;
	border-radius: 20rpx;
	font-size: 28rpx;
	// 暗黑风格
	background-color: rgba(0, 0, 0, .7);
	color: #fff;
	border: 5rpx solid rgba(255, 255, 255, .9)
}

.top-img {
	position: fixed;
	height: 300rpx;
	width: 100%;
	top: 0;
	left: 0;
	z-index: 19;
	// 切割图片，只显示上半部分200rpx
	overflow: hidden;
	// 背景图片
	background-image: url("https://file.kantboot.com/image/cover.png");
	// 背景图片大小
	background-size: 100% auto;
	background-position: top;
	// mode="aspectFill" 时，图片高度大于宽度时，图片宽度等于容器宽度，高度等比缩放，反之亦然
	background-repeat: no-repeat;
}

.top-box {
	position: fixed;
	width: 100%;
	height: 600rpx;
	top: 0;
	z-index: -1;

	.top-box-back-img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
	}

	.top-box-back {
		background-color: rgba(0, 0, 0, .1);
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		z-index: -1;
		left: 0;
	}



	.create-time {
		position: absolute;
		display: flex;
		align-items: center;
		margin-top: 30rpx;
		font-size: 24rpx;
		color: #f8f8f8;
		bottom: 20rpx;
		right: 20rpx;
		z-index: 2;
	}
}

.ovo-user-info-item {
	color: #fff;
	border-radius: 10rpx;
	padding: 10rpx 30rpx 10rpx 30rpx;
	box-sizing: border-box;

	.ovo-user-info-item-title {
		font-size: 30rpx;
		font-weight: bold;
		margin-bottom: 30rpx;
	}

	.ovo-user-info-item-content {
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: center;

		.ovo-user-info-item-content-item {

			background-color: rgba(255, 255, 255, .2);

			border-radius: 10rpx;
			padding: 10rpx 20rpx;
			margin-right: 20rpx;
			margin-bottom: 20rpx;


			.ovo-user-info-item-content-item-text {
				font-size: 24rpx;
			}
		}
	}

}

.segmentation-box {
	display: flex;
	justify-content: space-around;
	align-items: center;
	background-color: #fff;
	padding: 20rpx 0 20rpx 0;


	.segmentation-box-item {
		font-size: 30rpx;
		padding-bottom: 14rpx;
		color: #999;
	}

	.segmentation-box-item-active {
		color: #333;
		padding-bottom: 10rpx;
		border-bottom: 4rpx solid #333;
	}
}
</style>
